import { Collapse } from "@taroify/core"
import { QuestionOutlined, ShopOutlined } from "@taroify/icons"
import * as React from "react"
import { useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicCollapse() {
  const [value, setValue] = useState([0])
  return (
    <Collapse value={value} onChange={setValue}>
      <Collapse.Item title="1"></Collapse.Item>
      <Collapse.Item title="2"></Collapse.Item>
      <Collapse.Item title="3"></Collapse.Item>
    </Collapse>
  )
}

function AccordionCollapse() {
  return (
    <Collapse accordion defaultValue={0}>
      <Collapse.Item title="1"></Collapse.Item>
      <Collapse.Item title="2"></Collapse.Item>
      <Collapse.Item title="3"></Collapse.Item>
    </Collapse>
  )
}

function CollapseWithDisabledWithReadonly() {
  return (
    <Collapse defaultValue={[0]}>
      <Collapse.Item title=""></Collapse.Item>
      <Collapse.Item title="" clickable={false}>
        
      </Collapse.Item>
      <Collapse.Item title="" disabled>
        
      </Collapse.Item>
    </Collapse>
  )
}

function CustomCollapse() {
  return (
    <Collapse defaultValue={[0]}>
      <Collapse.Item
        className="custom-collapse-item1"
        title={
          <>
            1
            <QuestionOutlined />
          </>
        }
      >
        
      </Collapse.Item>
      <Collapse.Item icon={<ShopOutlined />} title="2" extra="" clickable={false}>
        
      </Collapse.Item>
    </Collapse>
  )
}

export default function CollapseDemo() {
  return (
    <Page title="Collapse " className="collapse-demo">
      <Block title="">
        <BasicCollapse />
      </Block>
      <Block title="">
        <AccordionCollapse />
      </Block>
      <Block title="">
        <CollapseWithDisabledWithReadonly />
      </Block>
      <Block title="">
        <CustomCollapse />
      </Block>
    </Page>
  )
}
